.App {
  text-align: center;
}

.App-header {
  background-color: #c5050c; /* UW red */
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
  padding: 2rem;
}

.App-logo {
  height: 120px;
  margin-bottom: 1rem;
}

h1 {
  font-size: 2.5rem;
  font-family: Georgia, 'Times New Roman', serif;
  margin: 0.5rem 0;
}

p {
  font-size: 1.2rem;
  max-width: 600px;
  margin-bottom: 2rem;
  font-family: Arial, sans-serif;
}

.App-links {
  display: flex;
  gap: 1.5rem;
}

.App-links a {
  background: white;
  color: #c5050c;
  padding: 0.8rem 1.2rem;
  border-radius: 8px;
  text-decoration: none;
  font-weight: bold;
  transition: all 0.3s ease;
}

.App-links a:hover {
  background: #282828;
  color: #fff;
}
